import CustomElement from './CustomElement';
import ContactsPopover from '../components/ContactsPopover';
const ATTRIBUTES = {
  serviceUrl: 'service-url'
};

class ContactElement extends CustomElement {
  static TAG = 'dw-contact-widget';

  constructor() {
    super(ATTRIBUTES, ContactElement);
  }

  get children() {
    const attrs = this.getAllAttributes();
    return <ContactsPopover {...attrs} />
  }

  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }
}

if (!customElements.get(ContactElement.TAG)) {
  customElements.define(ContactElement.TAG, ContactElement);
}