import React, { useRef, useState } from 'react';
import PropTypes from 'prop-types';
import { Box, MenuItem, ListItemIcon, ListItemText, Link } from '@mui/material';
import MenuPopover from '../mui-theme/components/MenuPopover';
import { alpha } from '@mui/material/styles';
import { MIconButton } from '../mui-theme/@material-extend';
import { useAssetPath } from '../hooks/AssetPath';


const ChooseLanguageWidget = ({ languages, currentLang, menuLeanRight }) => {
  const pathPrefix = useAssetPath();
  const languageToIconMap = {
    en: `${pathPrefix}static/icons/flag_en.svg`,
    de: `${pathPrefix}static/icons/flag_de.svg`,
    fr: `${pathPrefix}static/icons/flag_fr.svg`,
    us: `${pathPrefix}static/icons/flag_us.svg`,
    kr: `${pathPrefix}static/icons/flag_kr.svg`,
    it: `${pathPrefix}static/icons/flag_fr.svg`, // Change this icon with the real icon
  }


  const anchorRef = useRef(null);
  const [open, setOpen] = useState(false);
  console.log(languages);
  console.log(currentLang);
  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <React.StrictMode>
      <MIconButton
        ref={anchorRef}
        onClick={handleOpen}
        sx={{
          padding: 0,
          width: 44,
          height: 44,
          ...(open && {
            bgcolor: (theme) => alpha(theme.palette.primary.main, theme.palette.action.focusOpacity)
          })
        }}
      >
        <img src={languageToIconMap[currentLang]} alt={currentLang} />
      </MIconButton>

      <MenuPopover open={open} onClose={handleClose} anchorEl={anchorRef.current} sx={{ py: 1 }}>
        {languages.map((option) => (
          <MenuItem
            key={option.code}
            selected={option.code === currentLang}
            sx={{ py: 1, px: 2.5 }}
            component={Link}
            href={option.url}
          >
            <ListItemIcon>
              <Box component="img" alt={option.descr} src={languageToIconMap[option.code]} />
            </ListItemIcon>
            <ListItemText primaryTypographyProps={{ variant: 'body2' }}>{option.descr}</ListItemText>
          </MenuItem>
        ))}
      </MenuPopover>
    </React.StrictMode>
  )
};

ChooseLanguageWidget.propTypes = {
  languages: PropTypes.arrayOf(
    PropTypes.shape({
      code: PropTypes.string,
      descr: PropTypes.string,
      url: PropTypes.string,
    }),
  ),
  currentLang: PropTypes.string,
  menuLeanRight: PropTypes.bool,
};

ChooseLanguageWidget.defaultProps = {
  languages: [],
  currentLang: '',
  menuLeanRight: false,
};

export default ChooseLanguageWidget;
