import React from 'react';
import AccountPopover from '../components/AccountPopover';
import CustomElement from './CustomElement';

const ATTRIBUTES = {
  sessionUser: 'session-user',
  userDisplayName: 'user-display-name',
  redirectUrl: 'redirect-url',
  adminUrl: 'admin-url',
};

class AccountWidgetElement extends CustomElement {

  static TAG = 'dw-account-widget';
  constructor() {
    super(ATTRIBUTES, AccountWidgetElement);
  }

  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }

  get children() {
    const attrs = this.getAllAttributes();
    return <AccountPopover
      {...attrs}
    />;
  }
}

if (!customElements.get(AccountWidgetElement.TAG)) {
  customElements.define(AccountWidgetElement.TAG, AccountWidgetElement);
}

export default AccountWidgetElement;
