import React from 'react';
import ChooseLanguage from '../components/ChooseLanguageWidget';
import CustomElement from './CustomElement';

const ATTRIBUTES = {
  languages: 'languages',
  currentLang: 'current-lang',
};

class ChooseLanguageElement extends CustomElement {
  constructor() {
    super(ATTRIBUTES, ChooseLanguageElement);
  }
  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }


  get children() {
    let attrs = this.getAllAttributes();
    attrs = {
      ...attrs,
      languages: JSON.parse(attrs.languages)
    }
    return <ChooseLanguage
      {...attrs}
    />;
  }
}

if (!customElements.get('dw-choose-language-widget')) {
  customElements.define('dw-choose-language-widget', ChooseLanguageElement);
}

export default ChooseLanguageElement;
