import { Icon } from '@iconify/react';
import { useRef, useState, useCallback, useEffect } from 'react';
import peopleFill from '@iconify/icons-eva/people-fill';
// material
import { alpha } from '@mui/material/styles';
import { Avatar, Typography, ListItemText, ListItemButton, ListItemAvatar } from '@mui/material';
// hooks
import useIsMountedRef from '../mui-theme/hooks/useIsMountedRef';

// components
import Scrollbar from '../mui-theme/components/Scrollbar';
import MenuPopover from '../mui-theme/components/MenuPopover';
import BadgeStatus from '../mui-theme/components/BadgeStatus';
import { MIconButton } from '../mui-theme/@material-extend';

import { formatDistanceToNow, sub } from 'date-fns';
import { sample } from 'lodash';

const contactsMock = [...Array(20)].map((_, index) => ({
  id: index,
  name: "John Doe",
  avatar: null,
  lastActivity: sub(new Date(), { days: index, hours: index }),
  status: sample(['online', 'offline', 'away', 'busy']) || 'online',
}));

// ----------------------------------------------------------------------
function fToNow(date) {
  return formatDistanceToNow(new Date(date), {
    addSuffix: true
  });
}
const ITEM_HEIGHT = 64;
const PADDING_ITEM = 2.5;

export default function ContactsPopover() {
  const anchorRef = useRef(null);
  const isMountedRef = useIsMountedRef();
  const [open, setOpen] = useState(false);
  const [contacts, setContacts] = useState(contactsMock);

  const getContacts = useCallback(async () => {
    // try {
    //   const response = await axios.get('/api/chat/contacts');
    //   if (isMountedRef.current) {
    //     setContacts(response.data.contacts);
    //   }
    // } catch (error) {
    //   console.error(error);
    // }
  }, [isMountedRef]);

  useEffect(() => {
    getContacts();
  }, [getContacts]);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <>
      <MIconButton
        ref={anchorRef}
        size="large"
        color={open ? 'primary' : 'default'}
        onClick={handleOpen}
        sx={{
          ...(open && {
            bgcolor: (theme) => alpha(theme.palette.primary.main, theme.palette.action.focusOpacity)
          })
        }}
      >
        <Icon icon={peopleFill} width={20} height={20} />
      </MIconButton>

      <MenuPopover open={open} onClose={handleClose} anchorEl={anchorRef.current} sx={{ width: 360 }}>
        <Typography variant="h6" sx={{ p: PADDING_ITEM }}>
          Contacts <Typography component="span">({contacts.length})</Typography>
        </Typography>

        <Scrollbar sx={{ height: ITEM_HEIGHT * 8 }}>
          {contacts.map((contact) => {
            const { id, name, avatar, status, lastActivity } = contact;

            return (
              <ListItemButton key={id} sx={{ px: PADDING_ITEM, height: ITEM_HEIGHT }}>
                <ListItemAvatar sx={{ position: 'relative' }}>
                  <Avatar src={avatar} />
                  <BadgeStatus status={status} sx={{ position: 'absolute', right: 1, bottom: 1 }} />
                </ListItemAvatar>
                <ListItemText
                  primaryTypographyProps={{ typography: 'subtitle2', mb: 0.25 }}
                  secondaryTypographyProps={{ typography: 'caption' }}
                  primary={name}
                  secondary={status === 'offline' && fToNow(lastActivity)}
                />
              </ListItemButton>
            );
          })}
        </Scrollbar>
      </MenuPopover>
    </>
  );
}
