import CustomElement from './CustomElement';
import NotificationsPopover from '../components/NotificationsPopover';
const ATTRIBUTES = {
  serviceUrl: 'service-url'
};

class NotificationElement extends CustomElement {
  static TAG = 'dw-notification-widget'; // kebab-case

  constructor() {
    super(ATTRIBUTES, NotificationElement);
  }

  get children() {
    const attrs = this.getAllAttributes();
    return <NotificationsPopover {...attrs} />
  }

  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }
}

if (!customElements.get(NotificationElement.TAG)) {
  customElements.define(NotificationElement.TAG, NotificationElement);
}